import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from 'react-router-dom';
import AboutMe from './aboutme'
import Portfolio from './portfolio'
import WriteUps from './writeups'
import Blog from './blog'
import Home from './home'

function Header() {
    return (
        <Router>
        <header>
        <div class="header">
        <Link class="logo" to="/"><h1>Vanessa Lo</h1></Link>
        </div>

        <div class="wrapper">
        <nav>
        <ul>
            <li><Link  id="menu" to="/aboutme">/ABOUT ME</Link></li>
            <li><Link  id="menu" to="/blog">/BLOG</Link></li>
            <li><Link  id="menu" to="/writeups">/WRITE UPS</Link></li>
            <li><Link  id="menu" to="/portfolio">/PORTFOLIO</Link></li>
        </ul>
        </nav>
        </div>
        </header>
        <Switch>
            <Route path="/aboutme"><AboutMe /></Route>
            <Route path="/portfolio"><Portfolio /></Route>
            <Route path="/writeups"><WriteUps /></Route >
            <Route path="/blog"><Blog /></Route >
            <Route path="/"><Home /></Route >
        </Switch >
        </Router >
    );
}

export default Header;