import React from "react";

function AboutMe() {
    return (
        <section>
            <div class="wrapper">
                <h2 class="animated fadeInLeft">ABOUT ME</h2>
            </div>

            <div class="bottomwrapper animated fadeInRight">
                <div class="box">
                    <img id="profile" alt="face" src="images/profile.jpg"/>
                    <br></br><br></br>
                    <p2>Hi, I'm Vanessa. <br></br><br></br>
                        I am a Cyber Defense Analyst and Founder CEO of CyberSentio. I graduated from UTS with a Bachelor of Science in Information Technology majoring in Networking and Cybersecurity.<br></br><br></br>
                        My interests include:
                        <div>
                            <ul id="list">
                                <li>Cyber Security</li>
                                <li>Computer Hardware and Building</li>
                                <li>Music</li>
                                <li>Hiking</li>
                                <li>Photography</li>
                                <li>Sports</li>
                            </ul>
                        </div>
                        Having joined the UTS Cyber Security Society as of 2019, I have participated in university-hosted Capture the Flag (CTF) challenges and my interest in the Cyber Security sparked.
                                                        <br></br><br></br>
                                                            I am specifically interested in the everchanging nature of Cyber Security and the new challenges it poses as technology evolves, including cyber defence, data protection and governance within the cyberspace. I aim to find a solution for the various security issues present and upcoming - challenge drives innovation.
                    </p2>
                    <br></br><br></br>
                    <h4>SOCIAL LINKS</h4>
                    <center>
                        <div class="socialbar">
                            <a href="https://www.linkedin.com/company/cybersentio/"><img class="socials" src="images/icons/linkedin.png"/></a>
                            <a href="https://www.facebook.com/cybersentio/"><img class="socials" src="images/icons/facebook.png"/></a>
                            <a href="https://www.instagram.com/cybersentio/"><img class="socials" src="images/icons/instagram.png"/></a>
                            <a href="https://www.youtube.com/channel/UC6RFdZJ4_ZU0P_o0ko0OXNQ/"><img src="images/icons/youtube.png"/></a>
                        </div>
                    </center>
                </div>
            </div>
        </section>
    );
}

export default AboutMe;